import { Component, OnInit, Renderer2, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { DateFormatService } from 'src/app/core/services/dateFormat.service'
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import * as moment from 'moment';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { HeaderData } from 'src/app/main/general-ladger/models/post-transaction.model';
import { TransferApprovedSamplesConstant } from '../../constant/transfer-approved-samples.const';
import { TransferApprovedSamplesModel } from '../../models/transfer-approved-sample.modal';
import { TransferApprovedSamplesService } from '../../http/transfer-approved-samples.service';
@Component({
  selector: 'app-transfer-approved-samples',
  templateUrl: './transfer-approved-samples.component.html',
  styleUrls: ['./transfer-approved-samples.component.scss']
})
export class TransferApprovedSamplesComponent extends BaseGrid implements OnInit {
  public orginalGridList: any;
  public mbClickAddLine: boolean;
  public allRowsSelected: boolean = false;
  public mbInstructionDialog: boolean = false;
  public msInstrunction: string = '';
  public isValid: boolean;
  public getDataList(): void { }
  public gridDataList: any = [];
  public bmlfSelectedCallback = (args) => args.dataItem;
  public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
  public mbIsLoading: boolean = false;
  public msScreenName: string = 'Transfer Approved Samples';
  public mbChange: boolean = false;
  public msCustomDateFormat: string = "";
  public msFormMode: string = "I";
  /**---Object Initialize-----**/
  public mobjUserData: any = {};
  public mobjModel: TransferApprovedSamplesModel = new TransferApprovedSamplesModel();
  public gridDataListCol: any = TransferApprovedSamplesConstant.garrSampTransferApprvItemCol.map(ele => Object.assign({}, ele));
  public marrRowsSelected: any = [];
  public mnumLineItemRowIndex: number = -1;
  public miQtyPrecision: number = 0;
  public miQtyPrecisionFormat: string = 'n' + this.miQtyPrecision;
  public QtyMaxLen = 12;
  public miPricePrecision: number = 6;
  public miPricePrecisionFormat: string = 'n' + this.miPricePrecision;
  public marrItemSelectData: any = [];
  public mbdeleteLineItem: boolean = false;
  public mbCustomControl = false;
  /**---END : Lookup realted field----**/
  /**-------START: TASK Field ----**/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  /**-------END: TASK Field ------**/

  /**-----------START E-Signature Field----------**/
  public mbSignatureModal: boolean = false;
  public mobjSignatureData: any = {};
  public mbIsEsignature: boolean = false;
  public mbIsEsignADTPermision: boolean = false;
  public mbIsEsignAuditDialog: boolean = false;
  public mobjCommonAlertData: any = {};
  public mbCommonAlertDialog: boolean = false;
  /**-----------END E-Signature Field----------**/
  //public mskeyValue: string = ""; //FieldExtender
  @ViewChild('custom') customCtrl: any;
  @ViewChild('FieldExtender') FieldExtender: any; //FieldExtender

  constructor(public router: Router, public route: ActivatedRoute,
    public renderer: Renderer2,
    private lsService: LocalStorageService,
    public toastMessageService: ToastMessageService,
    private formService: TransferApprovedSamplesService,
    public dateFormatService: DateFormatService,
    public utilService: AppUtility,
    private lookupService: LookupService,
    public userFieldService: UserFieldService) {
    super(lsService, dateFormatService, userFieldService, toastMessageService, utilService);
  }

  ngOnInit(): void {
    /*--------- get screen permission --------*/
    this.utilService.getBMEScreePermission(this.msScreenName);
    /**--- Get default date format from service--- */
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    this.bmlfGetSampleData();
  }

  bmlfGetSampleData() {
    let pobjReqData = {
      ScreenName: 'Sales Opportunity',
      LookUpKey: 'GetApprovedsalesOppRequsition',
      Filter1: ''
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.mbCustomControl = true;
        this.msFormMode = "U";
        this.gridDataList = null;
        let pobjData = res.LookupData;
        this.gridDataList = pobjData;
      }
      else {
        this.gridDataList = "";
      }

    })
  }

  selectAllRows(e): void {
    if (e.target.checked) {
      this.allRowsSelected = true;
      this.gridDataList.forEach((ele) => {
        ele.Select = true;
      });
    } else {
      this.allRowsSelected = false;
      let piRowIndex: number = 0;
      this.gridDataList.forEach((ele) => {
        ele.Select = false;
        for (let i = 0; i < this.marrRowsSelected.length; i++) {
          if (this.marrRowsSelected[i] == piRowIndex) {
            ele.Select = true;
          }
        }
        ++piRowIndex;
      });
    }
  }

  selectSingleRow(e, rowIndex) {
    if (e.target.checked == false) {
      for (let i = 0; i < this.marrRowsSelected.length; i++) {
        if (this.marrRowsSelected[i] == rowIndex) {
          e.target.checked = true;
          //this.gridDataList[this.marrRowsSelected]['Select'] = 
        }
      }
    }
  }



  bmlfTransferApprover() {
    if (this.gridDataList.length == 0 || this.gridDataList == undefined) {
      this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('SAMPLETRANSFERAPPROVAL.MSG.NODATA')));
      return
    }
    var parrSelected: any = ""
    parrSelected = this.gridDataList.filter(x => x['Select'] == true);
    if (parrSelected.length < 1) {
      this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('SAMPLETRANSFERAPPROVAL.MSG.SELECTREQ')));
      return false;
    }
    else {
      this.bmlfSubmitData();
    }
  }

  bmlfSubmitData() {
    var pobJData = [];
    this.gridDataList.forEach(ele => {
      if (ele.Select == true) {
        pobJData.push({
          "SMTransactionNo": ele.SMTransactionNo,
          "SMRequestNo": ele.SMRequestNo,
          "Mode": 'Automatic'
        })
        this.marrItemSelectData = pobJData
      }
    })

    let psJData: any = {
      JSON: JSON.stringify({
        ParameterData: this.marrItemSelectData,
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    }
    if (!this.utilService.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return false;
    };
    this.mbIsLoading = true;
    this.formService.bmlfTransferSampleTransferApprovalData(psJData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.toastMessageService.notifyError("Server Error.");
      }

      if (res.Response[0].ErrorCode == "") {
        //  this.toastMessageService.notifySuccess(res.Response[0].ResponseText); Bug 183214
        if((res.Response[0].ResponseText).match(/<br>/gi).length>1){
          let psFilteredRes=(res.Response[0].ResponseText).replace(/<br>/g, ',');
          let psRemoveLastComma=psFilteredRes.substr(0,psFilteredRes.lastIndexOf(','));
          this.toastMessageService.notifyError(psRemoveLastComma);
      }
        else {
          let psFilteredRes=(res.Response[0].ResponseText).replace(/<br>/g, '');
          this.toastMessageService.notifySuccess(psFilteredRes);
        }
        this.bmlfGetSampleData();
      }
      else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
        this.bmlfRefresh(); //Bug 181755
        console.log(res);
      }
    }
    ).add(() => { this.mbIsLoading = false; })
  }

  bmlfInstructModal(value, key) {
    if (key == 'open') {
      this.mbInstructionDialog = true;
      this.msInstrunction = value;
    } else if (key == 'close') {
      this.mbInstructionDialog = false;
      this.msInstrunction = '';
    }
  }


  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/Transfer Approved Samples.pdf', '_blank');
  }

  //Close Form
  bmlfCloseForm() {
    this.router.navigate(["/main/dashboard"]);
  }
  // Refresh
  bmlfRefresh() {
    this.router.navigate(["/main/crm/transfer-approved-samples"]);
  }

  //Open Task MODAL
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = '';
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }
}
