import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { Observable } from 'rxjs';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({providedIn: 'root'})

export class SalesOpportinityMasterService {

  constructor(
    private httpExt: HttpCleintExtService,
     private appSettings:AppSettingsService) {
  }

  public bmlfonGetData(headerData: any): Observable<ODataModel> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetSalesOpportunityMaster`;
    return this.httpExt.post<ODataModel>(url, headerData, null);
  }

  
  public bmlfonSaveMasterData(headerData: any): Observable<ODataModel> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveSalesOpportunityMaster`;
    return this.httpExt.post<ODataModel>(url, headerData, null);
  }

  public bmlfonDeleteLineData(headerData: any): Observable<ODataModel> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteSalesOpportunityMaster`;
    return this.httpExt.post<ODataModel>(url, headerData, null);
  }

}  
