import { Component, OnInit } from '@angular/core';
import { LanTranslationLoaderService } from 'src/app/core/services/translation-loader.service';
import { locale as english } from './i18n/en';
import { locale as hindi } from './i18n/hi';

import { locale as Chinese } from './i18n/zh';
import { locale as French } from './i18n/fr';
import { locale as Spanish } from './i18n/es';
import { locale as Portuguese } from './i18n/pt';
import { locale as Vietnamese } from './i18n/vi';

@Component({
  selector: 'app-crm',
  templateUrl: './crm.component.html'
})
export class CRMComponent implements OnInit {

  constructor(
    private _lanTranslationLoaderService: LanTranslationLoaderService
  ) {

    this._lanTranslationLoaderService.loadTranslations(english, hindi, Chinese, French, Spanish, Portuguese, Vietnamese );  }

  ngOnInit(): void {
  }

}