import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
})
export class SalesOpportunityTransactionService {

    constructor(
        private httpExt: HttpCleintExtService,
        private appSettings: AppSettingsService
    ) { }
    public bmlfSaveSalesOpportunityTranData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveSalesOpportunityTransaction`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfGetSalesOpportunityMenuList(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetMenuScreen`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfGetSalesOpportunityData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetSalesOpportunityTransaction`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfDeleteSalesOpportunityRowData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteSalesOpportunityTransaction`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfGetSalesOppQuotationData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetSalesOpportunityQuaotation`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfGetSalesOppInvoicesData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetSalesOpportunityInvoices`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}