import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard, WMSAuthGuard } from 'src/app/core/guards/auth.guard';
import { CRMComponent } from './crm.component';
import { SalesOpportunityMasterComponent } from './components/sales-opportunity-master/sales-opportunity-master/sales-opportunity-master.component';
import { SalesOpportunityTransactionListComponent } from './components/sales-oppportunity-transaction/sales-opportunity-transaction-list/sales-opportunity-transaction-list.component';
import { SalesOpportunityTransactionManageComponent } from './components/sales-oppportunity-transaction/sales-opportunity-transaction-manage/sales-opportunity-transaction-manage.component';
import { TransferApprovedSamplesComponent } from './components/transfer-approved-samples/transfer-approved-samples.component';
const routes: Routes = [
  {
    path: '',
    component: CRMComponent,
    children:
      [
        {
          path: '', redirectTo: 'crm', pathMatch: 'full'
        },
        {
          path: 'sales-opportunity-master', component: SalesOpportunityMasterComponent, canActivate: [AuthGuard]
        }, {
          path: 'sales-opportunity-transaction', component: SalesOpportunityTransactionListComponent, canActivate: [AuthGuard]
        }, {
          path: 'sales-opportunity-transaction/manage', component: SalesOpportunityTransactionManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'transfer-approved-samples', component: TransferApprovedSamplesComponent, canActivate: [AuthGuard]
        }

      ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CRMRoutingModule { }